import Vue from 'vue';
import Vuex from 'vuex';
import actions from './actions';
import getters from './getters';
import mutations from './mutations';

Vue.use(Vuex);
const store = new Vuex.Store({
    state: {
        isLogin: false, // 判断用户是否登录的标识
        isBLR: 0, // 用于判断用户注册登录绑定行为,0-bind,1-login,2-register
        showLoginBox: false, // 用于全局登陆框的显示与否
        mobileNbr: '',
        password: '',
        userCode: '',
        tokenCode: '',
        localCity: '郑州市', // 默认城市
        latitude: '30.878131',
        longitude: '120.137198',
        isChooseCity: false, // 用于判别用户是否自己选择城市
        zoneId: '', // 商圈id
        userCouponList: [],
        global: {
            hftcom: 'https://cloud.hfq.huift.com.cn',
            hftcomClient: 'https://cloud.hfq.huift.com.cn/Api/Client',
            hftcomShop: 'https://cloud.hfq.huift.com.cn/Api/Shop',
            hftcomWeixinCard: 'https://cloud.hfq.huift.com.cn/Api/WeiXinCard',
            hftcomEnCode: 'http%3a%2f%2f192.168.5.118%3a2000%2f%23%2f',
            hftcomOpenID: 'https://cloud.hfq.huift.com.cn/Admin/OpenId/getUserOpenId',
            hftcomGetUserInfo: 'https://cloud.hfq.huift.com.cn/Api/Shop',
            Daipay: 'https://cloud.hfq.huift.com.cn/Admin/Lcy/Daipay',
            Dipay: 'https://cloud.hfq.huift.com.cn/Admin/Lcy/DiPay',
            getAT: 'https://cloud.hfq.huift.com.cn/Admin/OpenId/getAccessToken'
        },
        weixin: {
            openId: '',
            code: '',
            htmldata: ''
        },
        indexCouponList: [], // 首页使用的优惠券详情列表
        indexCouponDetailsInfo: {}, // 未领取的优惠券详情页单个信息
        CouponListInfo: [], // 首页传过来得所有已获取已收藏的券信息
        collectShopList: [], // 收藏的商户列表
        isbool: true, // 首页使用的判断
        searchIsBool: true,
        searchShopList: [], // 查询页面使用的shop集合
        nearbyIsBool: true, // 附近页面使用的判断
        nearbyShopList: [],  // 附近页面使用的shop集合
        searchConponIsBool: true, // 优惠券查询页使用的判断
        searchConponList: [], // 优惠券查询页面使用的shop集合
        searchTypeValue: 0, // 首页选择相关行业时 的行业类型 默认为0  全部类型
        searchTypeName: '行业',  // 首页选择相关行业时 的行业名称   用来传递
        currentOrderInfo: [], // 当前正在提交订单的优惠券信息
        isCancelOrder: false
    },
    getters,
    mutations,
    actions
});
export default store;



